.p{
    width:30%;
    height: 50vh;
    margin: 20px 10px;
    border: 2px solid rgb(243, 242, 242);
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
}

.p-browser{
    height: 20px;
    background-color: rgb(243, 242, 242);
    display: flex;
    align-items: center;
    position: sticky;
    z-index: 1;
}

.p-circle{
    width: 6px;
    height: 6px;
    border-radius: 50%;
    margin: 3px;
    background-color: #fff;
}

.p-img{
    width: 80%;
    transition: all 0.5s ease;
}

.p:hover .p-img{
    transform: translateY(-10%);
}


@media screen and (max-width: 480px){
    .p{
      width: 40%;
      height: 20vh;
    }
}