.pl{
    padding: 50px 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.pl-texts{
    width: 65%;
}

/*.pl-title{}*/

.pl-desc{
    margin: 20px 0px;
}

.pl-list{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
}

@media screen and (max-width: 480px){
    .pl{
       padding: 10px;
    }

    .pl-texts{
        width: 100%;
    }

}